*{
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;}


#navbarNavDropdown {
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
}

#navbarNavDropdown li {
  padding: 0px 10px;
}


.nav-item{
  font-size: 1.3rem;
  transition-property: all;
  transition-duration: .2s;
}
.nav-item:hover{
  font-size: 1.4rem;


}
#navbar{
  display: flex;
  padding: 0px 40px;
  position: sticky;
  top: 8rem;
  left: 0px;
  width: auto;
  margin: 0px 2px;
  z-index: 3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: darkgray;
}
#navbar ul {
  display: flex;
  text-decoration: none;
  justify-content: center;
  align-items: baseline
}

#navbar ul li {
  list-style: none;
  color: #555;
  font-size: 1.5rem;
  padding: 0px 30px;
  transition: all;
  transition-duration: .2s;
}
#navbar ul li:hover{
  font-size: 1.6rem;
  color: #eff;
}

@media(max-width:480px){
  #navbar ul li {
    list-style: none;
    color: #555;
    font-size: 1.1rem;
    padding: 0px 15px;
    transition: all;
    transition-duration: .2s;
  }
  #navbar ul li:hover{
    font-size: 1.2rem;
    color: #eff;
  }
}



