.me {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 1%;
  margin-top: 5%;
  margin-right: 3rem;
  margin-bottom: 3rem;
  height: 16rem;

}

.me h2 {
  font-size: 3.5rem;
  color: black;
  margin-bottom: 20px;
  margin-top: 1rem;
  text-align: center;
}

.me span {
  color: #d4b080;
}

.me p {
  color: #555;
  line-height: 2rem;
  font-size: 1.3rem;
  text-align: center;

}

.me a {
  background-color: #555;
  padding: 10px 18px;
  text-decoration: none;
  font-weight: bold;
  color: #ceebc8;
  display: inline-block;
  margin: 30px 0px;
  border-radius: 5px;
}


.images img {
  width: 31.5rem;
  height: 32.5rem;
  border-radius: 12rem;
  transition-property: all;
  transition-duration: .12s;

}

.images img:hover {
  width: 31.9rem;
  height: 32.9rem;
}

#deliv {

  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  flex-wrap: wrap;
}

.del {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: auto;
  margin-bottom: 1rem;
}

.del img {
  width: 4rem;
  margin-right: 1rem;
  transition-property: all;
  transition-duration: .1s;
}
.del img:hover{
  width: 4.1rem;
  animation-name: rotate;
  animation-duration: .1s;
  animation-iteration-count: 2;
 /* transform: rotate(10deg -12deg); */
 /* transform: rotate(-12deg); */
}

@keyframes rotate {
  from{
    transform: rotate(-5deg);
  }
  to{
    transform: rotate(5deg);
  }
  
}

#product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  flex-wrap: wrap;
}

.prod {
  display: flex;
  flex-direction: row;
  margin: 2px 2px;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.prod1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: 1px solid rgb(200, 178, 178);
  margin: 2px 4px;
  margin-bottom: 2rem;
  border-radius: 5px;
  width: 20rem;
  box-shadow: -1px 3px 7px -3px rgb(97, 96, 96);

  transition-property: all;
  transition-duration: .3s;
}

.prod1:hover {
  box-shadow: -1px 6px 20px -6px rgb(97, 96, 96);
  width: 20.5rem;
}

.prod1 P{
  font-weight: bold;
  font-size: 1.5rem;
}
.prod1 img {
  width: auto;
}

#owner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  flex-wrap: wrap;
}
.own{
  display: flex;
  flex-direction: row;
  margin: 2px 2px;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.owner1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: 1px solid rgb(200, 178, 178);
  margin: 2px 4px;
  margin-bottom: 2rem;
  border-radius: 5px;
  width: 21rem;
  box-shadow: -1px 3px 7px -3px rgb(97, 96, 96);

  transition-property: all;
  transition-duration: .3s;
}

.owner1:hover {
  box-shadow: -1px 6px 20px -6px rgb(97, 96, 96);
  width: 21.5rem;
}

.owner1 P{
  font-weight: bold;
  font-size: 1.2rem;
}
.owner1 img {
  width: auto;
}
.p1{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid wheat;
  margin: 2rem 6rem;
  width: 15rem;
  transition: all;
  transition-duration: .2s;
}
.p1:hover{
  transform: scale(1.2);
  z-index: 2;

}

.p1 img{
  width: inherit;
  height: 20rem;
}


